.banner{
   
    background-size:cover ;
    height: 490px;
    color:white;
   
}

.content{
    padding-top:160px ;
    height: 190px;
    padding-left: 15px;
   
}

.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.btn{
    color:white;
    outline:none;
    border:none;
    font-weight :700px;;
    border-radius: 5px;
    padding-top: .5rem;
    padding-bottom: 0.5rem;
    padding-left: 2rem;
    padding-right: 2rem;
    background-color: rgba(51,51,51,0.5) ;
    cursor: pointer;
    margin-right: 1rem;
     
}
.btn:hover{
    color:black;
    background-color: aliceblue;

}
.desc{
    width:45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height:80px; 
    max-width: 360px ;

     
}

.fade{
    height:9rem;
    background-image:linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}